import common from'../../util/common.js'
import code from'../../util/code.js'
import qs from "../../components/qs.js"
export default {
    data() {
        return {
			tabList:"",//错题
			time:'',//时间
			errorA:'',//是否显示再考一次 1显示 0 不显示
		}
    },
	onLoad() {
		var answerNV = uni.getStorageSync("answerCS");//答题次数
		var timeMode = uni.getStorageSync("timeModeS");//答题时间
		var startTV = uni.getStorageSync("startS");//开始时间
		var endTV = uni.getStorageSync("endS");//结束时间
		var nowTime = code.CurTime();//现在时间
		var answerId = uni.getStorageSync("answerIdS");
		if (answerId == "" || answerId == undefined || answerId == null || answerId == NaN || answerId == 'NaN') {
			uni.showToast({
				title: '请联系管理员查看考试成绩',
				icon: 'none',
				duration: 2000
			})
			this.errorA = 0;
			uni.clearStorage();
			return;	
		}else{
			var data =  {
				answer_id: answerId,
			}
			var answerData = qs.stringify(data);
			uni.request({
				url: common.port.mGrade,
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				method: "POST",
				dataType: "json",
				data:answerData,
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 500) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						})
						return
					} else {
						this.tabList = res.data;
						const timeV = code.DateToUnix(res.data.end_time) - code.DateToUnix(res.data.start_time);
						console.log(timeV);
						this.time = code.test(timeV);
						console.log(this.time)
					}
				}
			})
			if (timeMode == 1) { //任意时间
				if (answerNV == 1) {//仅一次
					this.errorA = 0
					uni.removeStorageSync('telS');
					uni.removeStorageSync('examNameS');
					uni.removeStorageSync('authorityS');
					uni.removeStorageSync('durationS');
					uni.removeStorageSync('pwdstartS');
					uni.removeStorageSync('radioScoreS');
					uni.removeStorageSync('minutsS');
					uni.removeStorageSync('checkScoreS');
					uni.removeStorageSync('judgeScoreS');
					uni.removeStorageSync('fillScoreS');
					uni.removeStorageSync('subScoreS');
					uni.removeStorageSync('finalResultS');
					uni.removeStorageSync('joinInfoS');
					uni.removeStorageSync('chuTime');
					uni.removeStorageSync('templateIdS');
				}else{
					this.errorA = 1
					uni.removeStorageSync('minutsS');
					uni.removeStorageSync('chuTime');
					uni.removeStorageSync('templateIdS');
					uni.removeStorageSync('finalResultS');
				}
			}else if (timeMode == 2) {//区间时间
				if (answerNV == 1) {//仅一次
					this.errorA = 0
					uni.removeStorageSync('telS');
					uni.removeStorageSync('examNameS');
					uni.removeStorageSync('authorityS');
					uni.removeStorageSync('durationS');
					uni.removeStorageSync('pwdstartS');
					uni.removeStorageSync('radioScoreS');
					uni.removeStorageSync('minutsS');
					uni.removeStorageSync('checkScoreS');
					uni.removeStorageSync('judgeScoreS');
					uni.removeStorageSync('fillScoreS');
					uni.removeStorageSync('subScoreS');
					uni.removeStorageSync('finalResultS');
					uni.removeStorageSync('joinInfoS');
					uni.removeStorageSync('chuTime');
					uni.removeStorageSync('templateIdS');
				}else{
					if(nowTime > startTV && nowTime < endTV){
						this.errorA = 1
						uni.removeStorageSync('minutsS');
						uni.removeStorageSync('chuTime');
						uni.removeStorageSync('templateIdS');
						uni.removeStorageSync('finalResultS');
					}else{
						this.errorA = 0
						uni.removeStorageSync('telS');
						uni.removeStorageSync('examNameS');
						uni.removeStorageSync('authorityS');
						uni.removeStorageSync('durationS');
						uni.removeStorageSync('pwdstartS');
						uni.removeStorageSync('radioScoreS');
						uni.removeStorageSync('minutsS');
						uni.removeStorageSync('checkScoreS');
						uni.removeStorageSync('judgeScoreS');
						uni.removeStorageSync('fillScoreS');
						uni.removeStorageSync('subScoreS');
						uni.removeStorageSync('finalResultS');
						uni.removeStorageSync('joinInfoS');
						uni.removeStorageSync('chuTime');
						uni.removeStorageSync('templateIdS');
					}
				}
				
			}else if (timeMode == 3) {//固定时间
				if (answerNV == 1) {//仅一次
					this.errorA = 0
					uni.removeStorageSync('telS');
					uni.removeStorageSync('examNameS');
					uni.removeStorageSync('authorityS');
					uni.removeStorageSync('durationS');
					uni.removeStorageSync('pwdstartS');
					uni.removeStorageSync('radioScoreS');
					uni.removeStorageSync('minutsS');
					uni.removeStorageSync('checkScoreS');
					uni.removeStorageSync('judgeScoreS');
					uni.removeStorageSync('fillScoreS');
					uni.removeStorageSync('subScoreS');
					uni.removeStorageSync('finalResultS');
					uni.removeStorageSync('joinInfoS');
					uni.removeStorageSync('chuTime');
					uni.removeStorageSync('templateIdS');
				}else{
					if(nowTime >= startTV){
						this.errorA = 1
						uni.removeStorageSync('minutsS');
						uni.removeStorageSync('chuTime');
						uni.removeStorageSync('templateIdS');
						uni.removeStorageSync('finalResultS');
					}else{
						this.errorA = 0
						uni.removeStorageSync('telS');
						uni.removeStorageSync('examNameS');
						uni.removeStorageSync('authorityS');
						uni.removeStorageSync('durationS');
						uni.removeStorageSync('pwdstartS');
						uni.removeStorageSync('radioScoreS');
						uni.removeStorageSync('minutsS');
						uni.removeStorageSync('checkScoreS');
						uni.removeStorageSync('judgeScoreS');
						uni.removeStorageSync('fillScoreS');
						uni.removeStorageSync('subScoreS');
						uni.removeStorageSync('finalResultS');
						uni.removeStorageSync('joinInfoS');
						uni.removeStorageSync('chuTime');
						uni.removeStorageSync('templateIdS');
					}
				}
			}
		}
	},
    methods: {
		// 查看错题
		 wrongTopic:function(){
			uni.navigateTo({
				url:'../mistakes/mistakes'
			})
		 },
		 // 在考一次
		 examAgain:function(){
			uni.navigateTo({
				url:'../notice/notice'
			}) 
			uni.removeStorageSync('gradeS');
			uni.removeStorageSync('wrongNum');
			uni.removeStorageSync('elapsedTime');
		 }
    }
}
